@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Proza+Libre:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navabar{
    /* background-image: url(https://c4.wallpaperflare.com/wallpaper/642/695/642/anime-demon-slayer-kimetsu-no-yaiba-giyuu-tomioka-hd-wallpaper-preview.jpg); */
    background-color:purple;
    width:100vw;
    background-repeat: no-repeat;
    background-size: 100%;
    position: fixed;
    top: 0;
    right:0;
    left:0;
    padding:10px;
    z-index:9999999999999;
    height: 80px;
    display:flex;
    align-items: center;
    justify-content:space-between;
    font-size:15px;
    font-family: 'Proza Libre', sans-serif;
    transition: all 300ms ease;
}

h1{
    margin:10px;
    cursor: pointer;
    color:white;
}

#search button{
    height:36.5px;
    margin:0;
    padding:3px;
    width:50px;
    border:1px solid black;
    border-left:0;
    border-top-right-radius: 6px;
    border-bottom-right-radius:6px;
    background-color:transparent;

}

#search button:hover > #icon{
animation:anime 2s ease infinite;
}
.navabar input{
    
    /* height:100%; */
    padding:10px;
    width:60%;
    border-bottom-left-radius: 6px;
    border-top-left-radius: 6px;
    border:1px solid black;
    outline:0;
    border-right:0;
}

#search{
    /* padding:10px; */
    display:flex;
    height:50px;
    margin-right:30px;
    align-items:center;
    justify-content:center;
    /* background-color:red; */
}
.class{
    height: 80px;
    display:flex;
    align-items: center;
    justify-content:space-between;
    font-size:15px;
    z-index: 999999999999999999999999999999999;
}

@keyframes anime{
    10%{
        margin-top:-2px;
    }
    30%{
        margin-top:-5px;
    }
    50%{
        margin-top:-4px;
    }
    70%{
        margin-top:-2px;
    }
    100%{
        margin-top:-1px;
    }
}
.main{
    font-family: 'Proza Libre', sans-serif;
    display: flex;
    flex-wrap:wrap;
    align-items:center;
    margin-top: 0px;
    justify-content: center;
    color:purple;
}
@media only screen and (max-width: 700px){
    .main{
        margin-top: 100px;
    }
}
.card{
    padding:1px;
    transition: all 300ms ease;
    position: relative;
    border-radius: 6px;
    margin: 7px;
    box-shadow: 1px 1px 0px rgba(0, 0, 0, 0.034), -2px -2px 5px white,
     2px 2px 3px gray,  -3px -5px 5px white;
    height:300px;
    width:200px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-color: purple;
}

.card:hover > #visible{
    visibility: visible;
}
.card h4{
    position:absolute;
    bottom:-10px;
    right:0;
    left:0;
    width:100%; 
    color:white;
}
.card img{
    
    top: 0;
    width:100%;
    left:0;
    height:80%;
}

#visible{
    position: absolute;
    top: 0;
    visibility: hidden;
    display:flex;
    align-items: center;
    justify-content: center;
    height:100%;
    font-size:20px;
    width:100%;
    background-color: rgba(0, 0, 0, 0.473);
}

#ic{
    font-size:80px;
    color:white;
}
.info{
    color:purple;
    margin-top:100px;
    font-family: 'Proza Libre', sans-serif;
    /* align-items:center;
    justify-content:center; */
}
#info-div img{
    float:left;
    margin:10px;
    width:300px;
    height:400px;
    border-radius: 6px;
}
h3{
    color:purple;
}

@media only screen and (max-width: 450px) {
	#info-div img{
        float:none;
    }
}
#episodes{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fit,5em);
    float:left;
    margin:10px;
   
}
#episodes button{
    font-family: 'Proza Libre', sans-serif;
    margin:2px;
    transition: all 300ms ease;
    padding:5px;
    background-color:yellow;
    border:1px solid transparent;
    outline: none;
    border-radius:6px;
    font-weight: bolder;
}
#episodes button:hover{
    background-color: purple;
    border: 1px solid white;
    color:white;
}
.genres{
    margin-top:100px;
    background-color:transparent;
    color:purple; 
    height: 50px;
    display:flex;
    align-items: center;
    justify-content: space-around;

    font-family: 'Proza Libre', sans-serif;
}
.genres p{
   font-size:20px;
   cursor:pointer;
   text-transform: capitalize;
}

@media only screen and (max-width: 700px){
	.genres{
        background-color:black;
        z-index:9999999999;
        display:block;
        position: fixed;
        left:0;
        bottom: 0;
        height:100vh;
        width:200px;
        top: 0;
        transform:translateX(-300px);
    }
}
